import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'hello',
      component: Hello
    },
    {
      path: '/hello',
      name: 'hello',
      component: require('../page/Hello')
    },
    {
      path: '/test',
      name: 'test',
      component: require('../page/test')
    }
  ]
})



